import React from 'react'

export default class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props)
    this.state = { hasError: false, error: null, info: null }
  }
  componentDidCatch(error, info) {
    console.error('[ErrorBoundary] Caught error', error, info)
    this.setState({ hasError: true, error, info })
  }
  render() {
    if (this.state.hasError) {
      return (
        <div style={{padding:20,background:'#041E33',color:'#fff',borderRadius:6}}>
          <h3 style={{marginTop:0}}>An error occurred</h3>
          <div style={{fontSize:13,whiteSpace:'pre-wrap'}}>{String(this.state.error)}</div>
          <details style={{marginTop:8,whiteSpace:'pre-wrap',color:'#ddd'}}>
            {this.state.info?.componentStack}
          </details>
        </div>
      )
    }
    return this.props.children
  }
}
